import React, {useState} from 'react'
//import axiosfrom 'axios'
import axios, {bUrl,cUrl,sUrl} from '../../../axioSer/axiosS.js';

import {isEmail} from '../../utils/validation/Validation'
import {showErrMsg, showSuccessMsg} from '../../utils/notification/Notification'
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";
// @material-ui/icons
import Email from "@material-ui/icons/Email";
import People from "@material-ui/icons/People";
// core components
import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import CardFooter from "components/Card/CardFooter.js";
import CustomInput from "components/CustomInput/CustomInput.js";

import styles from "assets/jss/material-kit-react/views/loginPage.js";

import image from "assets/img/bg7.jpg";

const useStyles = makeStyles(styles);



const initialState = {
    email: '',
    err: '',
    success: ''
}

function ForgotPassword(props) {
    
    const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
    setTimeout(function () {
    setCardAnimation("");
    }, 700);
    const classes = useStyles();
    const { ...rest } = props;

    const [data, setData] = useState(initialState)

    const {email, err, success} = data

    const handleChangeInput = e => {
        const {name, value} = e.target
        setData({...data, [name]:value, err: '', success: ''})
    }

    const forgotPassword = async () => {
        if(!isEmail(email))
            return setData({...data, err: 'Invalid emails.', success: ''})
            
        try {
            const res = await axios.post(`${bUrl}/user/forgot`, {email})

            return setData({...data, err: '', success: res.data.msg})
        } catch (err) {
            err.response.data.msg && setData({...data, err:  err.response.data.msg, success: ''})
        }
    }
    
    return (
        <div className="fg_pass">

            <Header
                absolute
                color="transparent"
                brand="Material Kit React"
                rightLinks={<HeaderLinks />}
                {...rest}
            />
            <div
                className={classes.pageHeader}
                style={{
                backgroundImage: "url(" + image + ")",
                backgroundSize: "cover",
                backgroundPosition: "top center",
                }}
            >
             <div className={classes.container}>
             <GridContainer justify="center">
                <GridItem xs={12} sm={12} md={8}>
                <Card className={classes[cardAnimaton]}>
                    <form className={classes.form}>
                        <CardHeader color="primary" className={classes.cardHeader}>
                            
                            <div className={classes.socialLine}></div>
                            <h2>Forgot Your Password?</h2>
                        </CardHeader>
                        <CardBody>
                        <div className="row">
                            

                            <CustomInput 
                                labelText="Email..."
                                id="email"
                                formControlProps={{
                                    fullWidth: true,
                                }}
                                
                                inputProps={{
                                    type: "email",
                                    endAdornment: (
                                    <InputAdornment  position="end">
                                        <Email className={classes.inputIconsColor} value={email} name="email" onChange={handleChangeInput}/>
                                    </InputAdornment>
                                    ),
                                }} 
                            value={email} name="email" onChange={handleChangeInput}
                            /> 
                            
                            {err && showErrMsg(err)}
                            {success && showSuccessMsg(success)}
                        </div>
                        </CardBody>
                        <CardFooter className={classes.cardFooter}>
                            <Button simple color="primary" size="lg" onClick={forgotPassword}>
                            Verify your email
                            </Button>
                        </CardFooter>
                        <CardFooter className={classes.Button}>
                        <Button href="/" simple color="primary" size="lg">
                            Get back to the login page
                        </Button>
                        </CardFooter>
                    </form>
                </Card>
                </GridItem>
            </GridContainer>
            </div>  </div>
        </div>
    )
}

export default ForgotPassword
 