import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Button from "@material-ui/core/Button";
import Hidden from "@material-ui/core/Hidden";
import Drawer from "@material-ui/core/Drawer";
// @material-ui/icons
import Menu from "@material-ui/icons/Menu";
// core components
import styles from "assets/jss/material-kit-react/components/headerStyle.js";


// My stuff---------------------------------------
import {Link} from 'react-router-dom'
import {useSelector} from 'react-redux'
//import axiosfrom 'axios'
import axios, {bUrl,cUrl,sUrl} from '../../axioSer/axiosS.js';

import './header.css'
//-----------------------------------------------


const useStyles = makeStyles(styles);

export default function Header(props) {
  const classes = useStyles();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  React.useEffect(() => {
    if (props.changeColorOnScroll) {
      window.addEventListener("scroll", headerColorChange);
    }
    return function cleanup() {
      if (props.changeColorOnScroll) {
        window.removeEventListener("scroll", headerColorChange);
      }
    };
  });
  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };
  const headerColorChange = () => {
    const { color, changeColorOnScroll } = props;
    const windowsScrollTop = window.pageYOffset;
    if (windowsScrollTop > changeColorOnScroll.height) {
      document.body
        .getElementsByTagName("header")[0]
        .classList.remove(classes[color]);
      document.body
        .getElementsByTagName("header")[0]
        .classList.add(classes[changeColorOnScroll.color]);
    } else {
      document.body
        .getElementsByTagName("header")[0]
        .classList.add(classes[color]);
      document.body
        .getElementsByTagName("header")[0]
        .classList.remove(classes[changeColorOnScroll.color]);
    }
  };
  const { color, rightLinks, leftLinks, brand, fixed, absolute } = props;
  const appBarClasses = classNames({
    [classes.appBar]: true,
    [classes[color]]: color,
    [classes.absolute]: absolute,
    [classes.fixed]: fixed,
  });
  
  const brandComponent = <Button className={classes.title}>{brand}</Button>;
  //-----------my stuff-----------------------
  // auth test
  const auth = useSelector(state => state.auth)
  const {user, isLogged} = auth

  // logOut handle function
  const handleLogout = async () => {
      try {
          await axios.get(`${bUrl}/user/logout`)
          localStorage.removeItem('firstLogin')
          window.location.href = "/";
      } catch (err) {
          window.location.href = "/";
      }
  }

  const userLink = () => {
    return <li className="drop-nav">
        
        <Link to="#" className="avatar">    
          <img src={user.avatar} alt=""/>{user.name}<i className="fas fa-angle-down"></i> 
        </Link>

        <ul className="dropdown">
          <Button className={classes.title}  href="/profile" > <Link to="/profile"></Link> Profile  </Button>
          <Button className={classes.title} to="/" onClick={handleLogout}>  Logout  </Button>
        </ul>
        
    </li>
  }

  const transForm = {
      transform: isLogged ? "translateY(-5px)" : 0
  }

  return (
    
    <AppBar className={appBarClasses}>

      <Toolbar className={classes.container}>

        {leftLinks !== undefined ? brandComponent : null}
        {/*----Left side-----court gate logo------------------*/}
        <ul style={transForm}>
          { isLogged ? userLink():<li><Link to="/"><i className="fas fa-user-graduate"></i>Not signed in</Link></li>} 
        </ul>
        <div className={classes.flex}>
          {leftLinks !== undefined ? (
            <Hidden smDown implementation="css">
              {leftLinks}
            </Hidden>
          ) : (
            brandComponent
          )}
        </div>
        {/*----right side-----court gate logo------------------*/}
        <Hidden smDown implementation="css">
          {rightLinks}
        </Hidden>
        {/*----right side---------?----------------------------*/}
        <Hidden mdUp>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerToggle}
          >
            <Menu />
          </IconButton>
        </Hidden>
        
      </Toolbar>
      

       {/*-------------------------?----------------------------*/}
      <Hidden mdUp implementation="js">
        <Drawer
          variant="temporary"
          anchor={"right"}
          open={mobileOpen}
          classes={{
            paper: classes.drawerPaper,
          }}
          onClose={handleDrawerToggle}
        >
          <div className={classes.appResponsive}>
            {leftLinks}
            {rightLinks}
          </div>
        </Drawer>
      </Hidden>
    
    </AppBar>

  );
}

Header.defaultProp = {
  color: "white",
};

Header.propTypes = {
  color: PropTypes.oneOf([
    "primary",
    "info",
    "success",
    "warning",
    "danger",
    "transparent",
    "white",
    "rose",
    "dark",
  ]),
  rightLinks: PropTypes.node,
  leftLinks: PropTypes.node,
  brand: PropTypes.string,
  fixed: PropTypes.bool,
  absolute: PropTypes.bool,
  // this will cause the sidebar to change the color from
  // props.color (see above) to changeColorOnScroll.color
  // when the window.pageYOffset is heigher or equal to
  // changeColorOnScroll.height and then when it is smaller than
  // changeColorOnScroll.height change it back to
  // props.color (see above)
  changeColorOnScroll: PropTypes.shape({
    height: PropTypes.number.isRequired,
    color: PropTypes.oneOf([
      "primary",
      "info",
      "success",
      "warning",
      "danger",
      "transparent",
      "white",
      "rose",
      "dark",
    ]).isRequired,
  }),
};
