import React, {useState} from 'react'
import { Link } from 'react-router-dom'
//import axiosfrom 'axios'
import axios, {bUrl,cUrl,sUrl} from '../../axioSer/axiosS.js';

import {showErrMsg, showSuccessMsg} from '../../components/utils/notification/Notification'
import {isEmpty, isEmail, isLength, isMatch} from '../../components/utils/validation/Validation'
//--------styling------------
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";
// @material-ui/icons
// @material-ui/icons
import Email from "@material-ui/icons/Email";
import People from "@material-ui/icons/People";
// core components
import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import CardFooter from "components/Card/CardFooter.js";
import CustomInput from "components/CustomInput/CustomInput.js";

import styles from "assets/jss/material-kit-react/views/loginPage.js";

import image from "assets/img/books.jpeg";

const useStyles = makeStyles(styles);
//---------------------------

const initialState = {
    name: '',
    email: '',
    password: '',
    cf_password: '',
    err: '',
    success: ''
}

function Register(props) {
    //-------styling

    const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
    setTimeout(function () {
        setCardAnimation("");
    }, 700);
    const classes = useStyles();
    const { ...rest } = props;


    //--------------
    const [user, setUser] = useState(initialState)

    const {name, email, password,cf_password, err, success} = user

    const handleChangeInput = e => {
        
        
        const {name, value} = e.target
        
        setUser({...user, [name]:value, err: '', success: ''})
    }


    const handleSubmit = async e => {
        
        e.preventDefault()
        if(isEmpty(name) || isEmpty(password))
            return setUser({...user, err: "Please fill in all fields.", success: ''})

        if(!isEmail(email))
            return setUser({...user, err: "Invalid emails.", success: ''})

        if(isLength(password))
            return setUser({...user, err: "Password must be at least 6 characters.", success: ''})
        
        if(!isMatch(password, cf_password))
            return setUser({...user, err: "Password did not match.", success: ''})

        try {
            
            const res = await axios.post(`${bUrl}/user/register`, {
                name, email, password
            })
            
            setUser({...user, err: '', success: res.data.msg})
        } catch (err) {
            
            
            err.response.data.msg && 
            setUser({...user, err: err.response.data.msg, success: ''})
        }
    }
    
    return (

        <div>

        <Header
          absolute
          color="transparent"
          brand="The court gate"
          rightLinks={<HeaderLinks />}
          {...rest}
        />
         
        <div
           onSubmit={handleSubmit}
           className={classes.pageHeader}
          style={{
            backgroundImage: "url(" + image + ")",
            backgroundSize: "cover",
            backgroundPosition: "top center",
          }}
        >
            
          <div className={classes.container}>
            <GridContainer justify="center">
              <GridItem xs={12} sm={12} md={10}>
                <Card className={classes[cardAnimaton]}>
                  <form className={classes.form}>
                    <CardHeader color="primary" className={classes.cardHeader}>
                      <h4>Register new student</h4>
                      <div className={classes.socialLine}>
                        
                      
                      </div>
                    </CardHeader>
                    <p className={classes.divider}>please enter your information below</p>
                    <CardBody>
                      <CustomInput
                        labelText="First Name..."
                        id="first"
                        formControlProps={{
                          fullWidth: true,
                        }}
                        inputProps={{
                          type: "text",
                          endAdornment: (
                            <InputAdornment position="end">
                              <People className={classes.inputIconsColor} />
                            </InputAdornment>
                          ),
                        }}
                        value={name} name="name" onChange={handleChangeInput}
                      />
                      <CustomInput
                        labelText="Email..."
                        id="email"
                        formControlProps={{
                          fullWidth: true,
                        }}
                        inputProps={{
                          type: "email",
                          endAdornment: (
                            <InputAdornment position="end">
                              <Email className={classes.inputIconsColor} />
                            </InputAdornment>
                          ),
                        }}
                        value={email} name="email" onChange={handleChangeInput}
                      />
                      <CustomInput
                        labelText="Password"
                        id="pass"
                        formControlProps={{
                          fullWidth: true,
                        }}
                        inputProps={{
                          type: "password",
                          endAdornment: (
                            <InputAdornment position="end">
                              <Icon className={classes.inputIconsColor}>
                                lock_outline
                              </Icon>
                            </InputAdornment>
                          ),
                          autoComplete: "off",
                        }}
                        value={password} name="password" onChange={handleChangeInput} 
                      />
                        <CustomInput
                        labelText="Confirm Password"
                        id="pass"
                        formControlProps={{
                          fullWidth: true,
                        }}
                        inputProps={{
                          type: "password",
                          endAdornment: (
                            <InputAdornment position="end">
                              <Icon className={classes.inputIconsColor}>
                                lock_outline
                              </Icon>
                            </InputAdornment>
                          ),
                          autoComplete: "off",
                        }}
                        value={cf_password} name="cf_password" onChange={handleChangeInput} 
                      />
                    </CardBody>
                    <CardFooter className={classes.cardFooter}>
                      <Button simple color="primary" size="lg" type="submit">
                        Submit
                      </Button>
                        
                    </CardFooter>
                        <div>{err && showErrMsg(err)}</div>
                        <div>{success && showSuccessMsg(success)}</div>
                    <CardFooter className={classes.Button}>
                      <Button href="/" simple color="primary" size="lg">
                        Get back to the login page
                      </Button>
                    </CardFooter>
                  </form>
                </Card>
              </GridItem>
            </GridContainer>
          </div>
          <Footer whiteFont />
        </div>
       
      </div>

       
    ) 
    
}

export default Register
