import React, {useState}  from "react";
//-------------------------------
import { Link, useHistory } from 'react-router-dom'
//import axiosfrom 'axios'
import axios, {bUrl,cUrl,sUrl}  from '../../axioSer/axiosS.js';

import {showErrMsg, showSuccessMsg} from '../../components/utils/notification/Notification';
import {dispatchLogin} from '../../redux/actions/authAction';
import {useDispatch} from 'react-redux'
import { GoogleLogin } from 'react-google-login';
import FacebookLogin from 'react-facebook-login';
//--------------------------------
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";
// @material-ui/icons
import Email from "@material-ui/icons/Email";
import People from "@material-ui/icons/People";
// core components
import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import CardFooter from "components/Card/CardFooter.js";
import CustomInput from "components/CustomInput/CustomInput.js";

import styles from "assets/jss/material-kit-react/views/loginPage.js";

import image from "assets/img/dubai.jpg";

const useStyles = makeStyles(styles);

const initialState = {
  email: '',
  password: '',
  err: '',
  success: ''
}

export default function LoginPage(props) {
  
  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");

  //--------------------------------------------------------
  const [user, setUser] = useState(initialState)
  const dispatch = useDispatch()
  
  const history = useHistory()

  const {email, password, err, success} = user

  const handleChangeInput = e => {
    
      const {name, value} = e.target
      
      setUser({...user, [name]:value, err: '', success: ''})
  }


  const handleSubmit = async e => {
    
    e.preventDefault()
    try {
      
      const res = await axios.post(`${bUrl}/user/login`, {email, password})

      setUser({...user, err: '', success: res.data.msg})
      localStorage.setItem('firstLogin', true)
      localStorage.setItem('logintoken',res.data.tkn)
      dispatch(dispatchLogin())
      history.push(`/`)

    } catch (err) {
 
        err.response.data.msg && 
        setUser({...user, err: err.response.data.msg, success: ''})
    }
  }

  const responseGoogle = async (response) => {
      try {
          const res = await axios.post(`${bUrl}/user/google_login`, {tokenId: response.tokenId})

          setUser({...user, error:'', success: res.data.msg})
          localStorage.setItem('firstLogin', true)

          dispatch(dispatchLogin())
          history.push(`/`)
      } catch (err) {
          err.response.data.msg && 
          setUser({...user, err: err.response.data.msg, success: ''})
      }
  }

  const responseFacebook = async (response) => {
    try {
        const {accessToken, userID} = response
        const res = await axios.post(`${bUrl}/user/facebook_login`, {accessToken, userID})

        setUser({...user, error:'', success: res.data.msg})
        localStorage.setItem('firstLogin', true)

        dispatch(dispatchLogin())
        history.push(`/`)
    } catch (err) {
        err.response.data.msg && 
        setUser({...user, err: err.response.data.msg, success: ''})
    }
  }
  //-------------------------------------------


  setTimeout(function () {
    setCardAnimation("");
  }, 700);
  const classes = useStyles();
  const { ...rest } = props;


  return (
    <div>
      <Header
        absolute
        color="transparent"
        brand="The court gate"
        rightLinks={<HeaderLinks />}
        {...rest}
      />
      
      <div 
        onSubmit={handleSubmit}
        className={classes.pageHeader}
        style={{
          backgroundImage: "url(" + image + ")",
          backgroundSize: "cover",
          backgroundPosition: "top center",
        }}
      >

        <div className={classes.container}>
          <GridContainer justify="center">
            <GridItem xs={12} sm={12} md={6}>
              <Card className={classes[cardAnimaton]}>
                <form className={classes.form}>

                  <CardHeader color="primary" className={classes.cardHeader}>
                    <h4>Login</h4>
                    <div className={classes.socialLine}>
                      <Button
                        justIcon
                        href="#pablo"
                        target="_blank"
                        color="transparent"
                        onClick={(e) => e.preventDefault()}
                      >
                        <i className={"fab fa-twitter"} />
                      </Button>
                      <Button
                        justIcon
                        href="#pablo"
                        target="_blank"
                        color="transparent"
                        onClick={(e) => e.preventDefault()}
                      >
                        <i className={"fab fa-facebook"} />
                      </Button>
                      <Button
                        justIcon
                        href="#pablo"
                        target="_blank"
                        color="transparent"
                        onClick={(e) => e.preventDefault()}
                      >
                        <i className={"fab fa-google-plus-g"} />
                      </Button>
                    </div>
                  </CardHeader>

                  <p className={classes.divider}>Or Be Classical</p>

                  {err && showErrMsg(err)}
                  {success && showSuccessMsg(success)}
                  <CardBody>

                    {/*<CustomInput
                      labelText="First Name..."
                      id="first"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      inputProps={{
                        type: "text",
                        endAdornment: (
                          <InputAdornment position="end">
                            <People className={classes.inputIconsColor} />
                          </InputAdornment>
                        ),
                      }}
                    />*/}
                    {/*
                      
                      <form >
                      <div>
                          <label htmlFor="email">Email Address</label>
                          <input type="text" placeholder="Enter email address" id="email"
                           value={email} name="email" onChange={handleChangeInput} />
                      </div>
      
                      <div>
                          <label htmlFor="password">Password</label>
                          <input type="password" placeholder="Enter password" id="password"
                          value={password} name="password" onChange={handleChangeInput} />
                      </div>
      
                     </form>
                    */}

                    {/*form*/}
                    {<CustomInput
                      labelText="Email..."
                      id="email"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      
                      inputProps={{
                        type: "email",
                        endAdornment: (
                          <InputAdornment  position="end">
                            <Email className={classes.inputIconsColor} value={email} name="email" onChange={handleChangeInput}/>
                          </InputAdornment>
                        ),
                      }} 
                      value={email} name="email" onChange={handleChangeInput}
                     > 

                      

                     </CustomInput>}

                    {<CustomInput
                      labelText="Password"
                      id="password"
                      formControlProps={{
                        fullWidth: true,
                      }}
                      
                      inputProps={{
                        type: "password",
                        endAdornment: (
                          <InputAdornment position="end">
                            
                            <Icon className={classes.inputIconsColor}>
                              lock_outline
                            </Icon>
                          </InputAdornment>
                        ),
                        autoComplete: "off",
                      }}
                      
                      value={password} name="password" onChange={handleChangeInput} 
                    />}
                     
                  </CardBody>
                  
        
                  <CardFooter className={classes.cardFooter}>
                    <Button simple color="primary" size="lg" type="submit">
                      Get started
                    </Button>
                    
                  </CardFooter>
                  <CardBody>
                    <li><Link to="/forgot_password">Forgot your password?</Link></li>
                    <li><Link to="/register">Register New Student</Link></li>
                   </CardBody>
                </form>
              </Card>
            </GridItem>
          </GridContainer>
        </div>



        <Footer whiteFont />
      </div>
      
    </div>
  );
}

