import React from 'react'
//import {Switch, Route} from 'react-router-dom'
import { Router, Route, Switch } from "react-router-dom";
import { createBrowserHistory } from "history";
import {useSelector} from 'react-redux'
// pages for this product
import Components from "../Components/Components.js";//1
import LandingPage from "../LandingPage/LandingPage.js";//2
import ProfilePage from "../ProfilePage/ProfilePage.js";//3
import LoginPage from "../LoginPage/LoginPage";//4

import Register from "../LoginPage/Register"//5
import ForgotPass from "../body/auth/ForgotPassword"//6
import ActivationEmail from "./auth/ActivationEmail"//7


import Profile from "../body/profile/Profile"//8
import editUer from "../body/profile/EditUser"//9
import NotFound from '../utils/NotFound/NotFound'
import ResetPass from "../body/auth/ResetPassword"

import Home from "../body/home/Home"

function Body() {
    var hist = createBrowserHistory();
    const auth = useSelector(state => state.auth)
    const {isLogged, isAdmin} = auth
    return (
        <section>
        <Router history={hist}>

            <Switch>
                <Route path="/Components" component={isLogged ? Components:NotFound} exact />{/*1*/}
                <Route path="/" component={isLogged ? LandingPage:LoginPage} exact/> {/*2*/}
                <Route path="/ProfilePage" component={isLogged ? ProfilePage:LoginPage} exact/>{/*3*/}
                <Route path="/LoginPage" component={!isLogged ? LoginPage:LandingPage} exact/>{/*4*/}
                <Route path="/register" component={!isLogged ?   Register:LandingPage} exact />{/*5*/}
                <Route path="/forgot_password" component={isLogged ? LandingPage : ForgotPass} exact />{/*6*/}
                <Route path="/user/activate/:activation_token" component={ActivationEmail} exact />{/*7*/}
                <Route path="/profile" component={isLogged ? Profile : LoginPage} exact />{/*8*/}
                <Route path="/editUer"  component={isLogged&isAdmin ? editUer : LoginPage} exact />{/*9*/}
            </Switch>

        </Router>,

            {/*<Switch>

                <Route path="/" component={Home} exact />

                <Route path="/login" component={isLogged ? NotFound : Login} exact />
                <Route path="/register" component={isLogged ? NotFound : Register} exact />

                <Route path="/forgot_password" component={isLogged ? NotFound : ForgotPass} exact />
                <Route path="/user/reset/:token" component={isLogged ? NotFound : ResetPass} exact />

                <Route path="/user/activate/:activation_token" component={ActivationEmail} exact />

                <Route path="/profile" component={isLogged ? Profile : NotFound} exact />
                <Route path="/edit_user/:id" component={isAdmin ? EditUser : NotFound} exact />

            </Switch>*/}
        </section>
    )
}

export default Body
