import React, {useState, useEffect} from 'react'
import {useParams, useHistory} from 'react-router-dom'
import {useSelector} from 'react-redux'
//import axiosfrom 'axios'
import axios, {bUrl,cUrl,sUrl} from '../../../axioSer/axiosS.js';
import {showSuccessMsg, showErrMsg} from '../../utils/notification/Notification'

//-----------------------------------------------------------------------------

// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons

// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Parallax from "components/Parallax/Parallax.js";

import styles from "assets/jss/material-kit-react/views/landingPage.js";

// Sections for this page
import ProductSection from "../../Components/Sections/ProductSection";
import TeamSection from "../../Components/Sections/TeamSection.js";
import WorkSection from "../../Components/Sections/WorkSection.js";

const dashboardRoutes = [];

const useStyles = makeStyles(styles);

export default function EditUser(props) {

    const {id} = useParams()
    const history = useHistory()
    const [editUser, setEditUser] = useState([])

    const users = useSelector(state => state.users)
    const token = useSelector(state => state.token)

    const [checkAdmin, setCheckAdmin] = useState(false)
    const [err, setErr] = useState(false)
    const [success, setSuccess] = useState(false)
    const [num, setNum] = useState(0)

    useEffect(() => {
        if(users.length !== 0){
            users.forEach(user => {
                if(user._id === id){
                    setEditUser(user)
                    setCheckAdmin(user.role === 1 ? true : false)
                }
            })
        }else{
            history.push(`/profile`)
        }
    },[users, id, history])

    const handleUpdate = async () => {
        try {
            if(num % 2 !== 0){
                const res = await axios.patch(`${bUrl}/user/update_role/${editUser._id}`, {
                    role: checkAdmin ? 1 : 0
                }, {
                    headers: {Authorization: token}
                })

                setSuccess(res.data.msg)
                setNum(0)
            }
        } catch (err) {
            err.response.data.msg && setErr(err.response.data.msg)
        }
    }

    const handleCheck = () => {
        setSuccess('')
        setErr('')
        setCheckAdmin(!checkAdmin)
        setNum(num + 1)
    }


  const classes = useStyles();
  const { ...rest } = props;

  return (
    <div>

      
      <Header
        color="transparent"
        routes={dashboardRoutes}
        brand="The court gate"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 200,
          color: "info",
        }}
        {...rest}
      />

      <Parallax filter image={require("assets/img/courts.jpg").default}>
        <div className={classes.container}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={6}>
              {
              //---------------------------------------------------------------------
             
                <div className="col-left">
                    <h2>Edit User</h2>

                    <div className="form-group">
                        <label htmlFor="name">Name</label>
                        <input type="text" name="name" defaultValue={editUser.name} disabled/>
                    </div>

                    <div className="form-group">
                        <label htmlFor="email">Email</label>
                        <input type="email" name="email" defaultValue={editUser.email} disabled />
                    </div>

                    <div className="form-group">
                        <input type="checkbox" id="isAdmin" checked={checkAdmin}
                        onChange={handleCheck} />
                        <label htmlFor="isAdmin">isAdmin</label>
                    </div>

                    <button onClick={handleUpdate}>Update</button>

                    {err && showErrMsg(err)}
                    {success && showSuccessMsg(success)}
                </div>

              //-------------------------------------------------------------------
              }
            </GridItem>
          </GridContainer>
        </div>
      </Parallax>
      
      
      <div className={classNames(classes.main, classes.mainRaised)}>
        <div className={classes.container}>
          <ProductSection /> 
          <TeamSection />
        </div>
      </div>
      
        <div className="row">
            <button onClick={() => history.goBack()} className="go_back">
                <i className="fas fa-long-arrow-alt-left"></i> Go Back
            </button>
        </div>

    <WorkSection />
    </div>
  );
}
